html {
    height: auto;
    width: 100%;
    background-color: var(--main-background-color);

    body {
        margin: 0;
        width: 100%;
        height: 100%;
        font-family: circular-light;
        box-sizing: border-box;

        #root {
            margin: 0;
            padding: 0;
            height: 100%;
            width: 100%;

            .app {
                height: fit-content;
                display: grid;
                grid-template-rows: 75px 1fr 75px;
                grid-template-columns: 1fr 8fr 1fr;
                color: var(--font-dark-color);
                transition: 0.6s;

                >* .full {
                    grid-row: 1/2;

                }
            
            }

            main {
                grid-row: 2/-2;
                grid-column: 2/-2;
            }
        }
    }
}

>*{
    box-sizing: border-box;
}