.reviews-modal {
    position: absolute;
    width: 64%;
    height: 80%;
    // top: 160%;
    right: 50%;
    transform: translatex(50%);

    border: 2px solid var(--font-lightgray-color);
    border-radius: 20px;
    background-color: var(--card-background-color);
    z-index: 100;

    .stay-reviews {
        height: 80%;
        border-radius: 20px;
        padding: 2%;
        padding-top: 10px;
        display: grid;
        grid-gap: 0 50px;
        grid-template-columns: 1.5fr 2fr;
        grid-template-rows: 10% 20% 70%;



        .review-states {
            grid-column: 1/2;
            grid-row: 2/3;
            position: relative;
            top: 10px;
            margin-top: 0px;

            h2 {
                display: block;
            }

            .stay-scores {
                display: flex;
                flex-direction: column;
                padding-left: 10px;
            }
        }

        .reviews-container {
            margin-top: 0;
            overflow: auto;
            height: 100%;
            grid-row: 1/-1;
        }

        .reviews-list-container {

            display: flex;
            flex-direction: column;
            overflow: auto;
            padding: 5%;
            padding-top: 0;
        }

        .review-preview-container {
            width: 100%;

            h5 {
                color: var(--font-gray-color);
            }

            p {
                padding-right: 10%;
            }

            p {
                line-height: 30px;
            }


        }
    }

    .modal-header {
        display: flex;
        justify-content: flex-end;
        height: 20px;
        padding-right: 15px;
        padding-top: 15px;

        & span {
            align-self: center;
            font-size: 24px;
            cursor: pointer;
            margin-right: 20px;

        }
    }

}

.screen {
    pointer-events: none;
    position: fixed;
    inset: 0;
    z-index: -1;
    height: 100vh;
    width: 100vw;

    &.screen-open {
        background-color: var(--font-gray-see-throw-color);
        pointer-events: all;
        z-index: 50;
    }
}

.icon-star {
    display: flex;
    align-items: center;
}

.stay-reviews-header {
    position: relative;
    top: 10px;
    display: flex;
    gap: 5px;
    align-items: center;
    margin-bottom: 40px;
    margin-top: 20px;

}