// Font Sizes
.fs12 {
    font-size: rem(12px);
}
.fs14 {
    font-size: rem(14px);
}
.fs16 {
    font-size: rem(16px);
}
.fs18 {
    font-size: rem(18px);
}
.fs20 {
    font-size: rem(20px);
}
.fs24 {
    font-size: rem(24px);
}
.fs28 {
    font-size: rem(28px);
}
.fs30 {
    font-size: rem(30px);
}
.dark {
    color: var(--font-dark-color)  !important;
}
.static{
    position: static ;
}
.isStay {
    padding: 0 10%;
}
.bold{
    font-family:  circular-medium;
}

.date{
    width: 75px;
    margin-left: 10px;
    margin-right: 10px;
}

// flex helpers
.flex {
    display: flex;

    > *.full-flex {
        flex: 1;
    }
    &.column {
        flex-direction: column;
    }
    &.justify-center {
        justify-content: center;
    }
    &.justify-start {
        justify-content: flex-start;
    }
    &.space-between {
        justify-content: space-between;
    }
    &.align-center {
        align-items: center;
    }
    &.align-end {
        align-items: flex-end;
    }
}

.separator{
    border-bottom: 1px solid var(--font-lightgray-color);
}
.stay-menu{
    right: 18% !important;
}
.end{
    align-self: flex-end;
}
.center{
    align-self: center;
}
.items-center{
justify-items: center;
}
.border-top{
    border-top:1px solid var(--font-lightgray-color);
}

.page-padding {
    padding-top: 81px;
    min-height: calc(100vh - 100px);
}


.square-ratio {
     aspect-ratio:(1,1);
}

.DateRangePickerInput_arrow {
    display: none;
}

.gray-box-shadow {
    padding-bottom: 5%;
    padding-top: 5%;
    box-shadow: inset 0 -0.5px 0 0 var(--font-lightgray-color);
}
.space{
    margin-bottom: 15px;
}
.hide {
    display: none;
}

.opened{
    display: block;
}

.sticky{
    position: fixed;
    top:50px;
}

.pointer{
    cursor: pointer;
}
